:root {
  --primary-color: #4748ee;
  --primary-color-dark: #4040d4;
  --secondary-color: #030b24;
  --secondary-color-light: #0e193c;
  --primary-aqua: #20caeb;
  --gray-color: #6b7080;
  --border-radius: 5px;
  --bg-color: #0e193c;
}

.container {
  width: 95%;
  margin: auto;
}

.cursor-pointer {
  cursor: pointer;
}

.icon_wrap {
  padding: 0.2rem;
  background: #f7f7f7;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.shadow {
  z-index: 100;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100vh;
  background: #0000001c;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal_close_btn {
  position: absolute;
  top: 15px;
  right: 15px;
}

.modal_close_btn svg {
  fill: rgb(182, 182, 182);
  width: 20px;
  height: 20px;
}

.main-modal {
  box-shadow: 1px 1px 5px 0 #0000002b;
  position: relative;
  width: 85%;
  max-width: 560px;
  min-height: 500px;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 2.5rem;
}

.hideModal {
  z-index: -1;
  opacity: 0;
}

.showModal {
  opacity: 1;
  z-index: 100;
  -webkit-animation: show 0.2s;
          animation: show 0.2s;
  -webkit-transform: scale(1);
          transform: scale(1);
}

@-webkit-keyframes show {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
    z-index: -1;
  }

  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
    z-index: 2;
  }
}

@keyframes show {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
    z-index: -1;
  }

  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
    z-index: 2;
  }
}

/* modal input icon */

.card-input {
  position: relative;
}

.card-input input {
  padding-left: 56px;
}

.card-input-icon {
  position: absolute;
  left: 10px;
  top: 10px;
  width: 24px;
  height: 24px;
}

.card-input-icon img {
  width: 100%;
  height: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

